@use "global" as *;

.campaign {
  position: relative;
  overflow: hidden;
}


.campaign__inner{
  position: relative;
  text-align: center;
  padding-right: 0;
  padding-left: 0;

  @include mq(md) {

  }
}

.campaign__swiper-next {
  position: absolute;
  display: none;

  @include mq(md) {
    display: block;
    top: rem(40);
    left: calc(50% + rem(500));
    transform: translate(-50%, -50%);
    height: rem(80);
    width: rem(80);
  }
}

.campaign__swiper-prev {
  position: absolute;
  display: none;

  @include mq(md) {
    display: block;
    top: rem(40);
    left: calc(50% + rem(340));
    transform: translate(-50%, -50%);
    height: rem(80);
    width: rem(80);
  }
}

.campaign__swiper-next :hover {
  opacity: 0.5;
}

.campaign__swiper-prev :hover {
  opacity: 0.5;
}

.campaign__swiper {
  margin-top: rem(24);
  margin-left: rem(8);

  @include mq(md) {
    margin-left: 0;
    display: flex;
    height: 100%;
    margin-right: calc(50% - 50vw);
  }
}

.campaign__slide {
  display: flex;
  max-width: rem(280);
  margin: 0;
  height: auto;

  @include mq(md) {
    max-width: rem(333);
    align-items: stretch;
    margin: 0;
  }
}




.campaign__slider {
  max-width: rem(600);
  padding-bottom: rem(5);

  @include mq(md) {
    right: 0;
    top: 0;
    max-width: 100%;
    height: 100%;
    padding-bottom: rem(5);
  }
}


.campaign__button {
  text-align: center;
  margin-top: rem(30);

  @include mq(md) {
    margin-top: rem(80);
  }
}